@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Montserrat", sans-serif}h1,h2,h3{color:#363636}h3,h4{font-weight:400;color:#646464}.App{transition:all 0.5s ease;-webkit-transition:all 0.5s ease;-moz-transition:all 0.5s ease;-ms-transition:all 0.5s ease;-o-transition:all 0.5s ease}.library-active{margin-left:30%}nav{min-height:10vh;display:flex;justify-content:space-around;align-items:center}nav button{background:transparent;cursor:pointer;border:none;border:2px solid #414141;padding:0.5rem;transition:all 0.3 ease;-webkit-transition:all 0.3 ease;-moz-transition:all 0.3 ease;-ms-transition:all 0.3 ease;-o-transition:all 0.3 ease}nav button:hover{background:#414141;color:#ffffff}@media screen and (max-width: 768px){nav button{z-index:120}}.song-container{min-height:60vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.song-container img{width:20%;height:auto;object-fit:contain;border-radius:50%;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%}.song-container img.playing{animation:rotation 10s infinite linear;-webkit-animation:rotation 10s infinite linear}.song-container h2{padding:3rem 1rem 1rem 1rem}.song-container h3{font-size:1rem}@-webkit-keyframes rotation{from{-webkit-transform:rotate(0deg)}to{-webkit-transform:rotate(359deg)}}@keyframes rotation{from{-webkit-transform:rotate(0deg)}to{-webkit-transform:rotate(359deg)}}@media screen and (max-width: 768px){.song-container img{width:60%;height:auto}}.player{min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.time-control{width:50%;display:flex}.time-control input{width:100%;-webkit-appearance:none;background:transparent;cursor:pointer}.time-control p{padding:0 1rem}.play-control{display:flex;justify-content:space-between;align-items:center;padding:1rem;width:30%}.play-control svg{cursor:pointer}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;height:16px;width:16px}input[type="range"]::-moz-range-thumb{-webkit-appearance:none;background:transparent;border:none}.track{background:lightblue;width:100%;height:1rem;position:relative;border-radius:1rem;-webkit-border-radius:1rem;-moz-border-radius:1rem;-ms-border-radius:1rem;-o-border-radius:1rem;overflow:hidden}.animate-track{background:#ccc;width:100%;height:100%;position:absolute;top:0;left:0;transform:translateX(0%);-webkit-transform:translateX(0%);-moz-transform:translateX(0%);-ms-transform:translateX(0%);-o-transform:translateX(0%);pointer-events:none}@media screen and (max-width: 768px){.time-control{width:90%}.play-control{width:60%}}.library{position:fixed;top:0;left:0;width:20rem;height:100%;overflow-x:hidden;overflow-y:overlay;box-shadow:2px 2px 50px #ccc;transform:translateX(-100%);-webkit-transform:translateX(-100%);-moz-transform:translateX(-100%);-ms-transform:translateX(-100%);-o-transform:translateX(-100%);transition:all 0.5s ease;-webkit-transition:all 0.5s ease;-moz-transition:all 0.5s ease;-ms-transition:all 0.5s ease;-o-transition:all 0.5s ease;opacity:0;z-index:100;background-color:#fff}.library.open{transform:translateX(0);-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);-o-transform:translateX(0);opacity:1;z-index:100}.library h2{padding:2rem}.library-song{display:flex;align-items:center;padding:1rem 2rem;cursor:pointer;transition:all 0.2s ease-in;-webkit-transition:all 0.2s ease-in;-moz-transition:all 0.2s ease-in;-ms-transition:all 0.2s ease-in;-o-transition:all 0.2s ease-in}.library-song:hover{background:#dedee1}.library-song.selected{background:#dedee1}.library-song img{width:30%}.song-description{display:flex;flex-direction:column;padding-left:1rem}.song-description.selected h3,.song-description.selected h4{color:white}.song-description h3{font-size:1rem;margin-bottom:0.2rem}.song-description h4{font-size:0.7rem}*{scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.5) transparent}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(155,155,155,0.5);border-radius:20px;-webkit-border-radius:20px;-moz-border-radius:20px;-ms-border-radius:20px;-o-border-radius:20px;border:transparent}@media screen and (max-width: 768px){.library{width:100%}}

